//preloader

(() => {

  const preloader = document.querySelector('.preloader');
  const tl = anime.timeline({
    autoplay: false,
  });

  document.body.classList.add('body-fixed');

  const isLoaded = () => {
    setTimeout(document.onload = () => {
      document.body.classList.remove('body-fixed');
      preloader.classList.toggle('hidden');
    }, 100);
  }

  if(preloader) {

    const preloaderDelay = {
      targets: preloader,
      duration: 1500,
    };

    const loaderFadeOut = {
      targets: preloader,
      opacity: {
        value: 0,
        duration: 300,
        easing: 'linear',
      },
      offset: '+=100',
      complete: function () {
        isLoaded();
      }
    };

    tl.add(preloaderDelay)
    .add(loaderFadeOut);

    tl.play();

  }

})();